/* Main app stylesheet. We use the BEM methodology for modular CSS.
 * For more information, visit: http://getbem.com/. We use the "b"
 * namespace for block elements
 */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@keyframes fullWidth {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
@keyframes fullHeight {
  from {
    height: 0;
  }
  to {
    height: 100%;
  }
}
.b-body {
  overflow-x: hidden;
  background-color: #fff;
}
.b-header {
  position: relative;
  width: 95vw;
  height: 95vh;
  margin: 2.5vh auto;
}
.b-header-content {
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 100%;
  height: 100%;
  opacity: 0;
  box-shadow: 3px 3px 2px rgba(17, 17, 17, 0.2), -3px -3px 2px rgba(17, 17, 17, 0.2);
}
.b-header-content__img {
  position: absolute;
  z-index: 0;
  bottom: 5%;
  left: 0;
}
@media (min-width: 576px) {
  .b-header-content__img {
    bottom: -20%;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-header-content__img {
    left: 10%;
    bottom: -40%;
  }
}
@media (min-width: 576px) and (min-width: 768px) and (min-width: 992px) {
  .b-header-content__img {
    bottom: -20%;
  }
}
.b-header-titlebox {
  position: relative;
  z-index: 10;
  width: 80%;
}
@media (min-width: 576px) {
  .b-header-titlebox {
    width: 70%;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-header-titlebox {
    width: 50%;
  }
}
.b-header-titlebox__title {
  position: relative;
  z-index: 10;
  padding: .5rem 1rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  font-size: 2.2rem;
  text-transform: uppercase;
  color: #fff;
  background-color: #111;
}
@media (min-width: 576px) {
  .b-header-titlebox__title {
    font-size: 2.8rem;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-header-titlebox__title {
    font-size: 3.5rem;
  }
}
.b-header-titlebox__decoration {
  position: absolute;
  z-index: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #6a107f;
  transform: rotate(5deg);
}
.b-header-slider {
  position: absolute;
  transition-duration: .5s;
}
.b-header-slider--vertical {
  width: .1rem;
}
.b-header-slider--horizontal {
  height: .1rem;
}
.b-header-slider--top {
  top: 0;
}
.b-header-slider--left {
  left: 0;
}
.b-header-slider--right {
  right: 0;
}
.b-header-slider--bottom {
  bottom: 0;
}
.b-header-slider--light-violet {
  background-color: #a91acc;
}
.b-phones-section,
.b-phone-highlight {
  display: flex;
  flex-flow: column wrap;
  padding: 2.5vh 2.5vw;
}
@media (min-width: 768px) {
  .b-phones-section,
  .b-phone-highlight {
    flex-flow: row nowrap;
    justify-content: space-between;
  }
}
.b-phones-card {
  min-height: 50vh;
  box-shadow: 2px 2px 1px rgba(17, 17, 17, 0.1), -2px -2px 1px rgba(17, 17, 17, 0.1);
  width: 100%;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .b-phones-card--small {
    width: 32vw;
  }
  .b-phones-card--large {
    width: 60vw;
  }
}
.b-phones-card--black {
  background-color: #111;
}
.b-phones-card--centered {
  display: flex;
  justify-content: center;
  align-items: center;
}
.b-phones-card--spaced {
  padding: 2rem;
}
.b-phones-card__label {
  width: 50%;
  padding: .5rem 1rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
}
.b-phones-card__label--white {
  background-color: #111;
  color: #fff;
}
.b-phones-card__label--black {
  background-color: #fff;
  color: #111;
}
.b-phones-card__text {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: .9rem;
}
.b-phones-card__text--black {
  color: #111;
}
.b-phones-card__text--white {
  color: #fff;
}
.b-phone-highlight {
  min-height: 100vh;
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: #444;
}
.b-highlight-card {
  position: relative;
  min-height: 50vh;
  width: 100%;
  overflow: hidden;
  background-color: #111;
  box-shadow: 2px 2px 1px rgba(17, 17, 17, 0.9), -2px -2px 1px rgba(17, 17, 17, 0.9);
}
@media (min-width: 768px) {
  .b-highlight-card {
    width: 46vw;
    margin-bottom: 0;
  }
}
.b-highlight-card:not(:last-child) {
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .b-highlight-card:not(:last-child) {
    margin-bottom: 0;
  }
}
.b-highlight-card--flex {
  display: flex;
}
.b-highlight-card--centered {
  justify-content: center;
  align-items: center;
}
.b-highlight-card--column {
  flex-flow: column nowrap;
}
.b-highlight-card--column-end {
  justify-content: space-between;
}
.b-highlight-card__label {
  width: 50%;
  padding: .5rem 1rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  z-index: 10;
}
.b-highlight-card__label--white {
  background-color: #111;
  color: #fff;
}
.b-highlight-card__label--black {
  background-color: #fff;
  color: #111;
}
@media (min-width: 768px) {
  .b-highlight-card__label {
    font-size: 2rem;
  }
}
.b-highlight-card__img {
  /* These adjustment will keep the phone image consistent throughout 
		 * viewports
		 */
  position: absolute;
  z-index: 0;
  top: -50%;
  left: 5%;
}
@media (min-width: 576px) {
  .b-highlight-card__img {
    top: -70%;
    left: 10%;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-highlight-card__img {
    top: -30%;
  }
}
.b-highlight-textbox {
  padding: 2rem;
}
.b-highlight-textbox__title {
  width: 50%;
  margin-left: auto;
  margin-bottom: 2rem;
  padding: .5rem 1rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #71377f;
}
.b-highlight-textbox__text {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  font-size: .9rem;
}
.b-highlight-textbox__text--black {
  color: #111;
}
.b-highlight-textbox__text--white {
  color: #fff;
}
.b-highlight-buttonbox {
  display: flex;
  flex-flow: row nowrap;
}
.b-highlight-button {
  display: block;
  width: 25%;
  border: none;
  padding: 1rem;
  font-size: 2rem;
  transition-duration: .5s;
  cursor: pointer;
}
.b-highlight-button:focus {
  outline: 1px solid #a91acc;
}
.b-highlight-button--inactive {
  color: #444;
  background-color: transparent;
}
.b-highlight-button--active {
  color: #6a107f;
  background-color: #fff;
}
.b-tablet-section {
  overflow: hidden;
  padding-top: 2rem;
  padding-right: 1rem;
  padding-left: 1rem;
}
@media (min-width: 768px) {
  .b-tablet-section {
    padding-top: 4rem;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .b-tablet-section {
    padding-top: 6rem;
  }
}
.b-tablet-section__title {
  margin-bottom: 1.5rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .b-tablet-section__title {
    font-size: 2.5rem;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .b-tablet-section__title {
    font-size: 3rem;
  }
}
.b-tablet-section__detached {
  padding-left: .5rem;
  padding-right: .5rem;
  color: #fff;
  background-color: #a91acc;
}
@media (min-width: 768px) {
  .b-tablet-section__detached {
    padding-left: .8rem;
    padding-right: .8rem;
  }
}
.b-tablet-section__text {
  font-family: 'Lato', sans-serif;
  font-weight: 700;
  text-align: center;
  color: #444;
}
@media (min-width: 576px) {
  .b-tablet-section__text {
    max-width: 35rem;
    margin-right: auto;
    margin-left: auto;
  }
}
.b-tablet-section__img {
  position: relative;
  top: 2rem;
  display: block;
  margin-right: auto;
  margin-left: auto;
}
@media (min-width: 768px) {
  .b-tablet-section__img {
    top: 5rem;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .b-tablet-section__img {
    top: 6rem;
  }
}
.b-video-section {
  padding: 3rem 2.5vw;
  background-color: #eee;
}
.b-video-section__title {
  width: 50%;
  padding: .5rem 1rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 2rem;
  font-size: 1.6rem;
}
.b-video-section__title--white {
  background-color: #111;
  color: #fff;
}
.b-video-section__title--black {
  background-color: #fff;
  color: #111;
}
@media (min-width: 576px) {
  .b-video-section__title {
    font-size: 2rem;
    margin-left: 10vw;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-video-section__title {
    font-size: 2.5rem;
  }
}
@media (min-width: 576px) and (min-width: 768px) and (min-width: 992px) {
  .b-video-section__title {
    font-size: 3rem;
  }
}
.b-video-section-platform {
  padding-top: 2rem;
  padding-bottom: 2rem;
  background-color: #fff;
  box-shadow: 3px 3px 2px rgba(17, 17, 17, 0.2), -3px -3px 2px rgba(17, 17, 17, 0.2);
}
@media (min-width: 576px) {
  .b-video-section-platform {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
@media (min-width: 576px) and (min-width: 992px) {
  .b-video-section-platform {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }
}
.b-videobox {
  position: relative;
  max-width: 50rem;
}
@media (min-width: 576px) {
  .b-videobox {
    width: 70%;
    margin-right: 10vw;
    margin-left: auto;
  }
}
.b-videobox__video {
  position: relative;
  z-index: 10;
  width: 100%;
  margin-bottom: 0;
}
.b-videobox-layer {
  position: absolute;
  z-index: 20;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 97.5%;
  background-color: rgba(17, 17, 17, 0.8);
}
@media (min-width: 768px) {
  .b-videobox-layer {
    height: 98%;
  }
}
@media (min-width: 768px) and (min-width: 992px) {
  .b-videobox-layer {
    height: 98.5%;
  }
}
.b-videobox-playbtn {
  border: 1px solid #fff;
  padding: 1rem;
  background-color: rgba(17, 17, 17, 0.9);
  box-shadow: 2px 2px 1px #111;
  cursor: pointer;
}
.b-videobox-playbtn:focus {
  border-color: #a91acc;
  outline: none;
  transition-duration: .4s;
}
.b-videobox-playbtn__icon {
  display: block;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: .5rem;
  font-size: 3rem;
  color: #71377f;
}
@media (min-width: 992px) {
  .b-videobox-playbtn__icon {
    font-size: 5rem;
  }
}
.b-videobox-playbtn__text {
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;
}
@media (min-width: 992px) {
  .b-videobox-playbtn__text {
    font-size: 1.5rem;
  }
}
.b-footer {
  padding: 2rem 1rem;
  background-color: #111;
}
@media (min-width: 768px) {
  .b-footer {
    padding-top: 3rem;
    padding-bottom: 3rem;
  }
}
.b-footer-listbox {
  display: flex;
  flex-flow: column nowrap;
  margin-right: auto;
  margin-left: auto;
  max-width: 30rem;
}
@media (min-width: 576px) {
  .b-footer-listbox {
    flex-flow: row nowrap;
    justify-content: space-around;
  }
}
@media (min-width: 576px) and (min-width: 768px) {
  .b-footer-listbox {
    max-width: 40rem;
  }
}
.b-footer-list-title {
  display: inline-block;
  padding: .2rem .5rem;
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-transform: capitalize;
  color: #111;
  background-color: #fff;
}
.b-footer-list {
  list-style-type: none;
  padding-left: 0;
}
.b-footer-list__link {
  font-family: 'Titillium Web', sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  color: #999;
  transition-duration: .3s;
}
.b-footer-list__link:hover {
  color: #a91acc;
}
